
.alert {
  position: fixed;
  top: 500px;
  left: 0;
  width: 100%;
  height: 30px;
  z-index: 999;
}
.alert p {
  margin: 30px;
  line-height: 50px;
  text-align: center;
  background: #f2dede;
  font-size: 18px;
  color: #a94442;
  border-radius: 4px;
}


