
.login .loginWrap {
  padding-top: 44px;
}
.login .loginWrap div {
  margin-top: 50px;
  padding: 40px;
}
.login .loginWrap div input {
  display: block;
  width: 100%;
  line-height: 24px;
  font-size: 16px;
  text-indent: 10px;
  outline: none;
  border: none;
  border-bottom: 1px solid #4fc08d;
}
.login .loginWrap div a {
  display: block;
  margin-top: 10px;
  width: 100%;
  line-height: 42px;
  font-size: 16px;
  text-align: center;
  background: #4fc08d;
  border-bottom: 2px solid #3aa373;
  color: #fff;
  border-radius: 4px;
}


