
.message {
  padding-top: 44px;
}
.message .msghead {
  height: 40px;
}
.message .msghead p {
  float: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 40px;
  width: 50%;
  line-height: 40px;
  text-align: center;
  font-size: 14px;
  font-weight: 700;
  border-bottom: solid 1px #d4d4d4;
}
.message .msghead p.noread {
  border-right: solid 1px #d4d4d4;
}
.message .msghead p.active {
  color: #ff5a5f;
  border-bottom: solid 2px #ff5a5f;
}
.message .msghead p a {
  display: block;
  width: 100%;
  height: 100%;
}
.message .msgbody .msg {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  border-bottom: 1px solid #d5dbdb;
}
.message .msgbody .msg img {
  display: block;
  width: 40px;
  height: 40px;
  margin-right: 15px;
  border-radius: 50%;
}
.message .msgbody .msg div {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.message .msgbody .msg div .name {
  line-height: 20px;
  font-size: 14px;
  font-weight: 600;
}
.message .msgbody .msg div .content {
  font-size: 12px;
  line-height: 20px;
  color: #333;
}


