
.user {
  padding-top: 44px;
}
.user .info {
  padding: 15px 0;
  background: #e7e7e7;
}
.user .info img {
  display: block;
  width: 100px;
  height: 100px;
  margin: 0 auto;
  border-radius: 50%;
}
.user .info p {
  line-height: 20px;
  text-align: center;
  font-size: 12px;
  color: #333;
}
.user .msghead {
  height: 40px;
}
.user .msghead p {
  float: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 40px;
  width: 50%;
  line-height: 40px;
  text-align: center;
  font-size: 14px;
  font-weight: 700;
  border-bottom: solid 1px #d4d4d4;
}
.user .msghead p.noread {
  border-right: solid 1px #d4d4d4;
}
.user .msghead p.active {
  color: #ff5a5f;
  border-bottom: solid 2px #ff5a5f;
}
.user .msgbody .msg {
  padding: 10px;
  border-bottom: 1px solid #d5dbdb;
}
.user .msgbody .msg p {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.user .msgbody .msg p.title {
  height: 30px;
  line-height: 30px;
  font-size: 14px;
  font-weight: 600;
  color: #7e8c8d;
}
.user .msgbody .msg p.time {
  height: 20px;
  line-height: 20px;
  font-size: 10px;
  text-align: right;
}
.user .msgbody .msg p strong {
  color: #42b983;
}
.user .loginout {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  font-size: 20px;
  font-weight: 600;
  line-height: 40px;
  text-align: center;
  background: #ff5a5f;
  color: #fff;
  z-index: 999;
}


