
.topic {
  height: 100%;
}
.topic.noscroll {
  overflow: hidden;
}
.topic .page {
  padding-top: 44px;
}
.topic .page .title {
  margin: 15px;
  padding: 5px;
  font-size: 18px;
  font-weight: 700;
  color: #2c3e50;
  line-height: 1.5;
  background-color: #f0f0f0;
  border-radius: 5px;
}
.topic .page .infoWrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 15px;
  height: 50px;
}
.topic .page .infoWrapper img {
  display: block;
  width: 45px;
  height: 45px;
  margin-right: 15px;
  border-radius: 50%;
}
.topic .page .infoWrapper .info {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.topic .page .infoWrapper .info p {
  height: 24px;
}
.topic .page .infoWrapper .info p .authorname,
.topic .page .infoWrapper .info p .createat {
  float: left;
  font-size: 12px;
  line-height: 24px;
}
.topic .page .infoWrapper .info p .label,
.topic .page .infoWrapper .info p .visitcount {
  float: right;
  font-size: 12px;
}
.topic .page .infoWrapper .info p .visitcount {
  line-height: 24px;
}
.topic .page .infoWrapper .info p .label {
  padding: 6px 12px;
  color: #fff;
  border-radius: 4px;
}
.topic .page .infoWrapper .info p .label.top {
  background: #e74c3c;
}
.topic .page .infoWrapper .info p .label.good {
  background: #e67e22;
}
.topic .page .infoWrapper .info p .label.share {
  background: #1abc9c;
}
.topic .page .infoWrapper .info p .label.ask {
  background: #3498db;
}
.topic .page .infoWrapper .info p .label.gob {
  background: #9b59b6;
}
.topic .page .content {
  padding: 15px;
  border-bottom: solid 1px #d4d4d4;
}
.topic .page .reply .replycount {
  padding: 15px;
  font-size: 16px;
  line-height: 24px;
  font-weight: 700;
  border-bottom: solid 1px #d4d4d4;
}
.topic .page .reply .replycount strong {
  color: #42b983;
  font-weight: 700;
}
.topic .page .reply .replyitem {
  padding: 15px;
  border-bottom: solid 1px #d4d4d4;
}
.topic .page .reply .replyitem .replyitemhead {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 48px;
}
.topic .page .reply .replyitem .replyitemhead img {
  display: block;
  width: 45px;
  height: 45px;
  margin-right: 15px;
  border-radius: 8px;
}
.topic .page .reply .replyitem .replyitemhead div {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.topic .page .reply .replyitem .replyitemhead div p {
  line-height: 24px;
}
.topic .page .reply .replyitem .replyitemhead div p:nth-child(2) {
  font-size: 16px;
  text-align: right;
}
.topic .page .reply .replyitem .replyitemhead div p:nth-child(2) span {
  margin-left: 20px;
}
.topic .page .reply .replyitem .replyitemhead div p .uped {
  color: #4fc08d;
}
.topic .page .reply .replyitem .replyitembody {
  padding: 15px 0;
}
.topic .page .reply .replybox {
  margin: 15px;
}
.topic .page .reply .replybox textarea {
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 15px;
  width: 100%;
  font-size: 14px;
  resize: none;
}
.topic .page .reply .replybox a {
  display: block;
  margin-top: 10px;
  width: 100%;
  line-height: 42px;
  font-size: 16px;
  text-align: center;
  background: #4fc08d;
  border-bottom: 2px solid #3aa373;
  color: #fff;
  border-radius: 4px;
}


