
.publish {
  padding-top: 44px;
}
.publish .publishwrap .category {
  padding: 15px;
  height: 30px;
  font-size: 0;
}
.publish .publishwrap .category span {
  display: inline-block;
  width: 30%;
  line-height: 30px;
  font-size: 12px;
  text-align: center;
}
.publish .publishwrap .category select {
  display: inline-block;
  width: 40%;
  height: 20px;
  margin-top: 5px;
  vertical-align: top;
}
.publish .publishwrap .category .btnwrap {
  display: inline-block;
  width: 30%;
  height: 30px;
  line-height: 30px;
  vertical-align: top;
  text-align: center;
}
.publish .publishwrap .category .btnwrap button {
  width: 60%;
  height: 24px;
  line-height: 24px;
  margin-top: 3px;
  vertical-align: top;
  background: #80bd01;
  color: #fff;
  border-radius: 4px;
  border: none;
  outline: none;
}
.publish .publishwrap .title {
  padding: 10px;
  border-top: 1px solid #d4d4d4;
  border-bottom: 1px solid #d4d4d4;
}
.publish .publishwrap .title input {
  width: 100%;
  line-height: 30px;
  fint-size: 18px;
  border: none;
  outline: none;
}
.publish .publishwrap .content {
  margin: 15px;
  padding: 15px;
  border: 1px solid #d4d4d4;
}
.publish .publishwrap .content textarea {
  width: 100%;
  border: none;
  resize: none;
  outline: none;
}


