
.topics {
  height: 100%;
}
.topics.noscroll {
  overflow: hidden;
}
.topics .page {
  padding-top: 44px;
}
.topics .page .topic {
  padding: 10px 15px;
  border-bottom: 1px solid #d5dbdb;
}
.topics .page .topic .titleWrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.topics .page .topic .titleWrap .label {
  padding: 6px;
  font-size: 12px;
  color: #fff;
  border-radius: 2px;
  margin-right: 10px;
}
.topics .page .topic .titleWrap .label.top {
  background: #e74c3c;
}
.topics .page .topic .titleWrap .label.good {
  background: #e67e22;
}
.topics .page .topic .titleWrap .label.share {
  background: #1abc9c;
}
.topics .page .topic .titleWrap .label.ask {
  background: #3498db;
}
.topics .page .topic .titleWrap .label.gob {
  background: #9b59b6;
}
.topics .page .topic .titleWrap .title {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 24px;
  line-height: 24px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-weight: 600;
}
.topics .page .topic .infoWrap {
  margin-top: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.topics .page .topic .infoWrap img {
  display: block;
  margin-right: 10px;
  width: 38px;
  height: 38px;
  border-radius: 50%;
}
.topics .page .topic .infoWrap .info {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 38px;
  font-size: 14px;
  line-height: 19px;
}
.topics .page .topic .infoWrap .info p {
  height: 19px;
}
.topics .page .topic .infoWrap .info .loginname,
.topics .page .topic .infoWrap .info .createat {
  float: left;
}
.topics .page .topic .infoWrap .info .replycount {
  float: right;
}
.topics .page .topic .infoWrap .info .replycount strong {
  font-weight: 600;
  color: #42b983;
}
.topics .page .topic .infoWrap .info .replyat {
  float: right;
}


