
.menu {
  position: fixed;
  top: 0;
  bottom: 0;
  left: -200px;
  padding: 30px;
  width: 140px;
  background: #fff;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  z-index: 999;
}
.user,
.about,
.msg {
  padding: 15px 0;
}
.link {
  display: block;
  line-height: 24px;
  text-align: center;
}
.link img {
  margin-right: 10px;
  display: inline-block;
  width: 24px;
  height: 24px;
  vertical-align: top;
  border-radius: 4px;
}
.link .go {
  float: right;
  margin-top: 4px;
  width: 10px;
  height: 16px;
}
.user {
  border-bottom: 1px solid #d4d4d4;
}
.about,
.msg {
  border-top: 1px solid #d4d4d4;
}
.theme {
  margin: 40px 0;
}
.theme .link {
  display: block;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-align: center;
  color: #313131;
}
.msg {
  position: relative;
}
.msg span {
  position: absolute;
  right: 20px;
  top: 15px;
  display: block;
  width: 14px;
  height: 14px;
  line-height: 14px;
  text-align: center;
  font-size: 8px;
  color: #fff;
  background: #42b983;
  border-radius: 50%;
}


