
.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 44px;
  background: #fff;
  border-bottom: 1px solid #e8e8e8;
  -webkit-box-shadow: 0 0 4px rgba(0,0,0,0.25);
          box-shadow: 0 0 4px rgba(0,0,0,0.25);
  -webkit-transition: 0.3s;
  transition: 0.3s;
  z-index: 9;
}
.header h1 {
  width: 100%;
  height: 100%;
  line-height: 44px;
  font-size: 16px;
  text-align: center;
}
.header img {
  position: absolute;
  left: 0;
  top: 0;
  width: 19px;
  height: 16px;
  padding: 14px 15px;
}
.header .release {
  position: absolute;
  right: 15px;
  top: 0;
  line-height: 44px;
  color: #42b983;
}


